import axios from 'axios'
let mockMatch
// mock
if (process.env.NODE_ENV === 'development') {
  mockMatch = require('@/mock').default
}

// TODO: ,
const baseURL = '/api'

// mock
const mockURL = '/mock'

const instance = axios.create({
  baseURL,
  //  16 
  timeout: 16000,
  headers: {
    'Content-Type': 'application/json;charset=UTF-8'
  }
})

//  token 
instance.interceptors.request.use(
  config => {
    if (process.env.NODE_ENV === 'development') {
      const url = config.url
      if (mockMatch(url)) {
        config.baseURL = mockURL
      }
    }
    // storetoken
    // config.headers['Authorization'] = `Bearer ${store.getters.getAccessToken}`

    // get  
    if (config.method === 'get') {
      const now = `${Date.now()}`
      if (config.params) {
        config.params[now] = now
      } else {
        const hasParams = config.url.includes('?')
        config.url = config.url + (hasParams ? '&' : '?') + `${now}=${now}`
      }
    }

    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// 
instance.interceptors.response.use(
  response => {
    return response
  },
  error => {
    return Promise.reject(error)
  }
)
/**
 *    status  code 200  then ,  catch
 * @param {*} options
 */
const request = (options = {}) => {
  return new Promise((resolve, reject) => {
    instance(options)
      .then(({ data, status, statusText }) => {
        if (status === 200) {
          resolve(data)
        } else {
          reject(statusText)
        }
      })
      .catch(result => {
        if (!result || !result.response) {
          reject(result)
        }
        const {
          response: { status, statusText, data = {} }
        } = result
        switch (status) {
          // 
          case 401:
            sessionStorage.clear()
            // eslint-disable-next-line prefer-promise-reject-errors
            reject('')
            break
          case 403:
            // eslint-disable-next-line prefer-promise-reject-errors
            reject('')
            break
          case 404:
            // eslint-disable-next-line prefer-promise-reject-errors
            reject('404')
            // 
            break
          default:
            reject(data.message || statusText)
            break
        }
      })
  })
}

const get = (url, params = {}) => {
  return request({
    url,
    method: 'get',
    params
  })
}

const put = (url, data = {}) => {
  return request({
    url,
    method: 'put',
    data
  })
}

const post = (url, data = {}) => {
  return request({
    url,
    method: 'post',
    data
  })
}

const del = (url, data = {}) => {
  return request({
    url,
    method: 'delete',
    data
  })
}
// cancelToken
;[request, get, post, put, del, instance].forEach(item => {
  item.getCancelToken = () => {
    return axios.CancelToken
  }
})

export { request, get, post, put, del, instance as axios, baseURL }



